import React from 'react'

// import Box from '../../../components/Box'
import Flex from '../../../components/Flex'
import ContentModule from '../../../components/ContentModule'
import Title from '../../../components/Title'

import Chart from './Chart22'

const WeatherOne = () => {
  return (
    <Flex flexDirection="column" height="100vh">
      <Title height="auto" titleWidth="60%" subTitle="" text="" />
      <ContentModule
        text={"near firm\n\n19001960Simon Webber"}
        source="BNEF, IRENA,  2020/6/30"
        sourceColor="black"
        title=""
        src={Chart}
        background="linear-gradient(#A9D668 0%, #FFFFFF 66%)"
        flex={1}
      />
    </Flex>
  )
}

export default WeatherOne
